import { Formik, Form, Field, ErrorMessage } from "formik";      
import * as Yup from 'yup';
import axios from 'axios';
import { SHA512 } from 'crypto-js';
import { useNavigate } from "react-router-dom";
import "../Auth.scss";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import { apiHost } from "../../../apiHost";

const validationSchema = Yup.object().shape({
    name: Yup.string()
        .required('   ')
        .min(2, '    2 ')
        .max(15, '     15 '),
    surname: Yup.string()
        .required('   ')
        .min(2, '    2 ')
        .max(15, '     15 '),
    email: Yup.string()
        .email('  Email')
        .required('   '),
    password: Yup.string()
        .min(7, ' 7 ')
        .max(22, '  22 ')
        .matches(/[0-9]/, '  ')
        .matches(/[a-zA-Z]/, '   ')
        .required(' '),
    confirmPassword: Yup.string()
        .oneOf([Yup.ref('password'), null], '  ')
        .required(' '),
})

export const Register = () =>{
    const navigation = useNavigate()

    const RegisterFunction = async (values) => {
        const hashedPassword = SHA512(values.password).toString();

        const body = {
          name: values.name,
          surname: values.surname,
          role: 'student',
          email: values.email,
          password: hashedPassword,
        };
      
        try {
          const response = await axios.post(`${apiHost}user/register`, body);
          localStorage.setItem('userToken', response.data.token)
          navigation('/')
        } catch (error) {
          console.error('  :', error.response.data); // 
          alert('      ')
        }
    };

    const GoBack = () => {
        navigation('/')
    }

    return(
        <div className="auth">
            
            <Formik
                initialValues={{
                    name: '',
                    surname: '',
                    email: '',
                    password: '',
                    confirmPassword: '',
                }}
                validationSchema={validationSchema}
                onSubmit={values =>{
                    RegisterFunction(values)
                }}
                className="formik"
            >
                <Form className="form">
                    <div className="form__head">
                        <FontAwesomeIcon icon={faArrowLeft} className="arrow" onClick={GoBack}/>
                        <h1 className="auth__title"></h1>
                    </div>
                    <div className="input-block">
                        <label htmlFor="name" className="label">:</label>
                        <Field type="text" id="name" name="name" className="input"/>
                        <ErrorMessage name="name" component="div" className="error"/>
                    </div>
                    <div className="input-block">
                        <label htmlFor="surname" className="label">:</label>
                        <Field type="text" id="surname" name="surname" className="input"/>
                        <ErrorMessage name="surname" component="div" className="error"/>
                    </div>
                    <div className="input-block">
                        <label htmlFor="email" className="label">:</label>
                        <Field type="email" id="email" name="email" className="input"/>
                        <ErrorMessage name="email" component="div"className="error"/>
                    </div>
                    <div className="input-block">
                        <label htmlFor="password" className="label">:</label>
                        <Field type="password" id="password" name="password" className="input"/>
                        <ErrorMessage name="password" component="div" className="error"/>
                    </div>
                    <div className="input-block">
                        <label htmlFor="confirmPassword" className="label"> :</label>
                        <Field type="password" id="confirmPassword" name="confirmPassword" className="input"/>
                        <ErrorMessage name="confirmPassword" component="div" className="error"/>
                    </div>
                    <button type="submit" className="form__btn"></button>
                </Form>
            </Formik>
        </div>    
    )
}