import { Formik, Form, Field, ErrorMessage } from "formik";      
import * as Yup from 'yup';
import axios from 'axios';
import { SHA512 } from 'crypto-js';
import { useNavigate } from "react-router-dom";
import "../Auth.scss";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import { apiHost } from "../../../apiHost";

const validationSchema = Yup.object().shape({
    email: Yup.string()
        .email('  Email')
        .required('   '),
    password: Yup.string()
        .min(7, ' 7 ')
        .max(22, '  22 ')
        .matches(/[0-9]/, '   ')
        .matches(/[a-zA-Z]/, '  ')
        .required(' '),
    confirmPassword: Yup.string()
        .oneOf([Yup.ref('password'), null], '  ')
        .required(' '),
})

export const Login = () =>{
    const navigation = useNavigate()

    const LoginFunction = async (values) => {
        const hashedPassword = SHA512(values.password).toString();

        const body = {
          email: values.email,
          password: hashedPassword,
        };
      
        try {
          const response = await axios.post(`${apiHost}user/login`, body);
          const token = response.data.token
          localStorage.setItem('userToken', token)
          navigation('/')
        } catch (error) {
          alert('  :  ,  ,  ', error.response.data); 
        }
    };

    const GoBack = () => {
        navigation('/')
    }

    const GoForgot = () => {
        navigation('/forgot')
    }

    return(
        <div className="auth">
            
            <Formik
                initialValues={{
                    email: '',
                    password: '',
                    confirmPassword: '',
                }}
                validationSchema={validationSchema}
                onSubmit={values =>{
                    LoginFunction(values)
                }}
                className="formik"
            >
                <Form className="form">
                    <div className="form__head">
                        <FontAwesomeIcon icon={faArrowLeft} className="arrow" onClick={GoBack}/>
                        <h1 className="auth__title"></h1>
                    </div>
                    <div className="input-block">
                        <label htmlFor="email" className="label">:</label>
                        <Field type="email" id="email" name="email" className="input"/>
                        <ErrorMessage name="email" component="div" className="error"/>
                    </div>
                    <div className="input-block">
                        <label htmlFor="password" className="label">:</label>
                        <Field type="password" id="password" name="password" className="input"/>
                        <ErrorMessage name="password" component="div" className="error"/>
                    </div>
                    <div className="input-block">
                        <label htmlFor="confirmPassword" className="label"> :</label>
                        <Field type="password" id="confirmPassword" name="confirmPassword" className="input"/>
                        <ErrorMessage name="confirmPassword" component="div" className="error"/>
                    </div>
                    <div className="input-block">
                        <div className="some__btn" onClick={GoForgot}> ?</div>
                    </div>
                    <button type="submit" className="form__btn"></button>
                </Form>
            </Formik>
        </div>    
    )
}