import React from 'react';
import "./Lesson.scss";
import { LessonBtn } from "./LessonBtn";

const Lesson5 = ( {lessonId, groupId} ) => {
    return (
        <div className="lesson-block">
            <h1 className="title-h1">.</h1>
            <div className="main-block">
                <div className="content">
                    <h3 className="content__title-h3">,      HTML Forms</h3>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">  ?</h3>
                    <div className="content__block">
                        <p className="content__paragraph">  HTML     -,       .        ,    .</p>
                        <p className="content__paragraph">   :</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;form action=".php" method="POST"&gt;<br />
                                &nbsp;&nbsp;&lt;label for="'"&gt;':&lt;/label&gt;<br />
                                &nbsp;&nbsp;&lt;input type="text" id="'" name="'" required /&gt;<br />
                                &nbsp;&nbsp;&lt;label for=""&gt;:&lt;/label&gt;<br />
                                &nbsp;&nbsp;&lt;input type="password" id="" name="" required /&gt;<br />
                                &nbsp;&nbsp;&lt;input type="submit" value="" /&gt;<br />
                                &lt;/form&gt;
                            </code>
                        </p>
                        <p className="content__paragraph">  :</p>
                        <p className="content__paragraph">
                            - action:  URL  .<br />
                            - method:  HTTP- (GET  POST)   .<br />
                            - name: '       .<br />
                            - type:   ,   "text"  "password".<br />
                            - id:         JavaScript  CSS.<br />
                            - label:       .
                        </p>
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">  </h3>
                    <div className="content__block">
                        <p className="content__paragraph">  (input type="text):</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;input type="text" name="username" id="username" placeholder="  '" required /&gt;
                            </code>
                        </p>
                        <p className="content__paragraph"> (input type="password):</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;input type="password" name="password" id="password" placeholder=" " required /&gt;
                            </code>
                        </p>
                        <p className="content__paragraph">  (textarea):</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;textarea name="comment" id="comment" placeholder=" "&gt;&lt;/textarea&gt;
                            </code>
                        </p>
                        <p className="content__paragraph">   (input type=radio):</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;input type="radio" name="gender" id="male" value="male" /&gt;<br />
                                &lt;label for="male"&gt;&lt;/label&gt;<br />
                                &lt;input type="radio" name="gender" id="female" value="female" /&gt;<br />
                                &lt;label for="female"&gt;&lt;/label&gt;
                            </code>
                        </p>
                        <p className="content__paragraph">   (input type=checkbox):</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;input type="checkbox" name="hobby" id="reading" value="reading" /&gt;<br />
                                &lt;label for="reading"&gt;&lt;/label&gt;<br />
                                &lt;input type="checkbox" name="hobby" id="gaming" value="gaming" /&gt;<br />
                                &lt;label for="gaming"&gt;  &lt;/label&gt;
                            </code>
                        </p>
                        <p className="content__paragraph">  (select):</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;select name="country" id="country"&gt;<br />
                                &nbsp;&lt;option value="usa"&gt; &lt;/option&gt;<br />
                                &nbsp;&lt;option value="canada"&gt;&lt;/option&gt;<br />
                                &nbsp;&lt;option value="uk"&gt; &lt;/option&gt;<br />
                                &lt;/select&gt;
                            </code>
                        </p>
                        <p className="content__paragraph">  (input type=submit):</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;input type="submit" value="" /&gt;
                            </code>
                        </p>
                        <p className="content__paragraph">    (input type=reset):</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;input type="reset" value="" /&gt;
                            </code>
                        </p>
                        <p className="content__paragraph">    (input type=file):</p>
                        <p className="content__paragraph">
                            <code>
                                &lt;input type="file" name="file" id="file" /&gt;
                            </code>
                        </p>
                    </div>
                </div>
                <div className="content">
                    <h2 className="content__title-h3"> </h2>
                    <div className="content__block">
                        <p className="content__paragraph">1.  :</p>
                        <a className="link" target="blank" href="https://docs.google.com/document/d/1m7Sb9s5yQ_juWCKrDWbz8pxbQ06Ni4BaCab-4jsI_Cc/edit?usp=sharing">  </a>
                    </div>
                    
                </div>
            </div>
            <LessonBtn lessonId={lessonId} groupId={groupId}></LessonBtn>
        </div>
    );
}

export default Lesson5;