import "./Lesson.scss";
import { LessonBtn } from "./LessonBtn";
import first from "./Lesson28_img/first.png"
import second from "./Lesson28_img/second.png"

export const Lesson28 = ( {lessonId, groupId} ) => {

    return(
        <div className="lesson-block">
            <h1 className="title-h1">Ajax</h1>
            <div className="main-block">
                <div className="content">
                    <h3 className="content__title-h3">,    ajax  .</h3>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">Json.</h3>
                    <div className="content__block">
                        <p className="content__paragraph">      JSON.           . </p>
                        <p className="content__paragraph">      JSON    JSON.stringify( ).</p>
                        <p className="content__paragraph">      JSON    JSON.parse ( ).</p>
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">.</h3>
                    <div className="content__block">
                        <p className="content__paragraph">Fetch   ,   .</p>  
                        <p className="content__paragraph">Catch   .</p> 
                        <img src={first} alt="#" className="img"/>  
                        <p className="content__paragraph">   ,       catch     JS.</p>       
                        <img src={second} alt="#" className="img"/>
                    </div>
                </div>
                <div className="content">
                    <h2 className="content__title-h3"> .</h2>
                    <div className="content__block">
                        <p className="content__paragraph">1.    .     <a className="link" target="blank" href="https://www.omdbapi.com/"></a>.       .</p>
                    </div>
                    
                </div>
            </div>
            <LessonBtn lessonId={lessonId} groupId={groupId}></LessonBtn>
        </div>    
    )
}

export default Lesson28