import "./Lesson.scss";
import { LessonBtn } from "./LessonBtn";
import first from "./Lesson24_img/first.png"
import second from "./Lesson24_img/second.png"
import third from "./Lesson24_img/third.png"
import four from "./Lesson24_img/four.png"
import five from "./Lesson24_img/five.png"
import six from "./Lesson24_img/six.png"
import seven from "./Lesson24_img/seven.png"
import eight from "./Lesson24_img/eight.png"

export const Lesson24 = ( {lessonId, groupId} ) => {

    return(
        <div className="lesson-block">
            <h1 className="title-h1"> 2.</h1>
            <div className="main-block">
                <div className="content">
                    <h3 className="content__title-h3">,        .</h3>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">Pop  Push:</h3>
                    <div className="content__block">
                        <p className="content__paragraph">pop():  pop()        .</p>
                        <p className="content__paragraph">push():  push()      .</p>
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">Shift  Unshift:</h3>
                    <div className="content__block">
                        <p className="content__paragraph">shift():  shift()        .</p>  
                        <p className="content__paragraph">unshift():  unshift()      .</p>            
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">Splice:</h3>
                    <div className="content__block">
                        <p className="content__paragraph">splice():  splice()     , ,    .</p>           
                        <p className="content__paragraph">.splice(_, __, [1, 2, ...]);</p>  
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">Map:</h3>
                    <div className="content__block">
                        <p className="content__paragraph">map():  map()   ,       .</p>   
                        <img src={first} alt="#" className="img"/>
                        <img src={second} alt="#" className="img"/>
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">Filter:</h3>
                    <div className="content__block">
                        <p className="content__paragraph">filter():  filter()   ,   ,    ,  .</p>           
                        <img src={third} alt="#" className="img"/>
                        <img src={four} alt="#" className="img"/>
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">Concat:</h3>
                    <div className="content__block">
                        <p className="content__paragraph">concat():  concat() '        .</p> 
                        <p className="content__paragraph">const  = 1.concat(2, 3, ...);</p>          
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">IndexOf  LastIndexOf:</h3>
                    <div className="content__block">
                        <p className="content__paragraph">indexOf():  indexOf()          -1,    .</p>           
                        <p className="content__paragraph">lastIndexOf():  lastIndexOf()          -1,    .</p> 
                        <img src={five} alt="#" className="img"/>
                        <img src={six} alt="#" className="img"/>
                        <img src={seven} alt="#" className="img"/>
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">Includes:</h3>
                    <div className="content__block">
                        <p className="content__paragraph">includes():  includes() ,        true  false.</p>           
                        <p className="content__paragraph">.includes(, _);</p> 
                        <img src={eight} alt="#" className="img"/>
                    </div>
                </div>
                <div className="content">
                    <h3 className="content__title-h3">  .</h3>
                    <div className="content__block">
                        <a className="link" target="blank" href="https://w3schoolsua.github.io/js/js_array_methods.html#gsc.tab=0"> 1.</a> 
                        <a className="link" target="blank" href="https://uk.javascript.info/array-methods"> 2.</a>         
                    </div>
                </div>
                <div className="content">
                    <h2 className="content__title-h3"> .</h2>
                    <div className="content__block">
                        <p className="content__paragraph"> .</p>
                        <p className="content__paragraph">1.   fruits   : "apple", "banana", "cherry", "date".   push(),      "grape".   .</p>
                        <p className="content__paragraph">2.   pop(),      fruits.   .</p>
                        <p className="content__paragraph">3.   numbers    1  5.   shift(),     .   .</p>
                        <p className="content__paragraph"> .</p>
                        <p className="content__paragraph">1.   colors   : "red", "green", "blue".   unshift(),      "yellow".   .</p>
                        <p className="content__paragraph">2.   grades   : [85, 92, 78, 90, 88].   splice(),          [95, 87]   .   .</p>
                        <p className="content__paragraph">3.   words  : "apple", "banana", "cherry", "date".   concat(),   ,   ' words   ["fig", "grape"].   .</p>
                        <p className="content__paragraph"> .</p>
                        <p className="content__paragraph">1.   numbers    1  10.   map(),    squaredNumbers,       .  squaredNumbers.</p>
                        <p className="content__paragraph">2.     ' ,        .   filter(),    ,        50.  .</p>
                        <p className="content__paragraph">3.      "abcdefg".   includes(), ,    "c"      .</p>
                    </div>
                    
                </div>
            </div>
            <LessonBtn lessonId={lessonId} groupId={groupId}></LessonBtn>
        </div>    
    )
}

export default Lesson24